import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";

// mint-ui
import Mint from "mint-ui";
// Axios
import Axios from "axios";
import VueAxios from "vue-axios";
import Qs from 'qs'; // 引入Qs，这个库是axios里面包含的，不需要再下载了
// Axios 设置
// 创建一个axios实例
var axios_instance = Axios.create({
    // config里面有这个transformRquest， 这个选项会在发送参数前进行处理。 这时候我们通过Qs.stringify转换为表单查询参数
    transformRequest: [function (data) {
        data = Qs.stringify(data);
        return data;
    }],
    // 设置Content - Type
    headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
    }
});
Vue.prototype.$iot_api = '/iot_api';
// Vue.prototype.$iot_api = 'http://iot.yuximi.com/public/index/index';


Vue.use(VueAxios, axios_instance);
Vue.use(Mint);


// mqtt
// import yxMqtt from "@/utils/yxMqtt";
// Vue.prototype.$yxMqtt = yxMqtt;

// eventHub
window.eventBus = new Vue();

Vue.config.productionTip = false;

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount("#app");