import Vue from "vue";
import Router from "vue-router";
// 主页面
import Home from "./views/Home.vue";
import Setting from "./views/Setting.vue";

// 设置 的 子页面


Vue.use(Router);

export default new Router({
    base: process.env.BASE_URL,
    routes: [{
            path: '/',
            // name: "home",
            component: Home
        },
        {
            path: '/setting',
            // name: "setting",
            component: Setting,
            children: [{
                    path: 'timingswitch',
                    // name: 'timingswitch',
                    component: () => import("./components/TimingSwitch.vue"),
                },
                {
                    path: 'airfilter',
                    // name: 'resetairfilter',
                    component: () => import("./components/ResetAirFilter.vue"),
                },
                {
                    path: 'eqpshare',
                    // name: 'eqpshare',
                    component: () => import("./components/EqpShare.vue"),
                },
            ],
        },
        {
            path: '/device',
            component: () => import("./views/device.vue"),
        }
    ]
});