import mqtt from "mqtt";
// import Vue from "vue";
// import store from '../../store';

/**
 * yxMqtt 
 * @param {Obj} param;
 * 
 * @param {String} hostname; 必须；         mqtt 连接地址
 * 
 * @param {Obj} options.username; 必须；    登陆 用户名
 * @param {Obj} options.passWord; 必须；    登陆 密码
 * @param {Obj} options.clientId; 必须；    连接 id 必须保持 唯一
 * 
 * @param {Function} arrived; 可选；        接收到消息的 回调函数 
 * @param {Function} error; 可选；          连接错误 返回错误 的 回调函数 
 */
const yxMqtt = function(param) {
    this.mqttParam = {
        hostname: '',
        options: {
            username: '',
            password: '',
            clientId: '',
        },
        clientId_basic: '',
        topic: {
            sub: '',
            pub: '',
        },
    };
    this.Client = null;
    this.arrived = null;
    this.error = null;

    this.init(param);
}
// 初始化 配置 参数
yxMqtt.prototype.init = function(param) {
    // console.log(param);
    let error = '';
    // 设置 hostname
    if ( 'hostname' in param && param.hostname != '') {
        this.mqttParam.hostname = param.hostname;
    }else {
        error = "'yxMqtt': 'hostnmae' must be a string.";
    }
    // 设置 options
    for (let key in this.mqttParam.options) {
        if ((key in param.options) && param.options[key] != '') { // options 参数必需要，且 不能为 空
            if (key == 'clientId') {
                // 同一台 设备，多个 客户端 必须 保持 clientId 唯一
                this.mqttParam.options.clientId = param.options.clientId +'_'+ randomString(32);
                this.mqttParam.clientId_basic = param.options.clientId;
            } else {
                this.mqttParam.options[key] = param.options[key];
            }
        } else {
            error = `'yxMqtt': Lack of paramete: '${key}';`;
        }

        if( error != '') break;
    }
    // 接收消息的 回调函数
    if( 'arrived' in param && typeof(param.arrived) == 'function' ) {
        this.arrived = param.arrived;
    }
    // 返回错误的 回调函数
    if( 'error' in param && typeof(param.error) == 'function' ) {
        this.error = param.error;
    }
    // Mqtt 主题
    if( 'sub' in param.topic && param.topic.sub != '' ){
        this.mqttParam.topic.sub = param.topic.sub;
    }
    if( 'pub' in param.topic && param.topic.pub != '' ){
        this.mqttParam.topic.pub = param.topic.pub;
    }

    if (error != '') {
        throw new Error(error);
    }
}
// 开始 订阅
yxMqtt.prototype.startSub = function() {
    // console.log(this.mqttParam);
    // console.log('startSub ');
    this.Client = mqtt.connect(this.mqttParam.hostname, this.mqttParam.options);
    // this.Client = mqtt.connect(this.mqttParam.hostname);
    this.Client.on("connect", () => {
        // console.log('connect');

        let topic = this.mqttParam.topic.sub;
        // console.log(topic);
        this.Client.subscribe(topic);

        // let topic2 = '$baidu/iot/shadow/gh_0ddfb722a565_c9edd422aafdc95f/get/accepted';
        // this.Client.subscribe(topic2);
        // console.log(topic);

        // this.Client.publish('$baidu/iot/shadow/gh_0ddfb722a565_c9edd422aafdc95f/get', JSON.stringify({ 'residired': {}, 'reported': {}}))
    }).on('error', err => {
        // console.log("链接mqtt报错", err);
        // 返回 错误 信息
        this.error(err);
        this.Client.end();
        this.Client.reconnect();
    }).on("message", (topic, message) => {
        // console.log('topic', topic);
        // console.log('message', message);

        // 返回 接收到的数据
        let result_message = Uint8ArrayToString(message);
        result_message = JSON.parse(result_message);
        let result = {
            "topic": topic,
            'message': result_message,
        }
        this.arrived(result);
    });

    // Vue.prototype.$yxMqtt = this.Client; // 方便在vue组件中可以直接使用this.$yxMqtt -> client
}
/**
 * 发送数据
 * @param {Json} payload ; {"status": 1, "test": "123aaa"}
 */
yxMqtt.prototype.publish = function(payload) {
    // 数据 格式 转换
    payload = JSON.stringify(payload);
    console.log(payload);
    var topic = this.mqttParam.topic.pub;
    
    this.Client.publish(topic, payload)
}
// 关闭订阅
yxMqtt.prototype.closeSub = function() {
    this.Client && this.Client.end();
};

/**
 * 获取 百度天工 物影子 订阅主题
 * @param {String} type; 订阅类型 例如：'/update/accepted'
 */
yxMqtt.prototype.baudushadow_topic = function (type) {
    var topic = `$baidu/iot/shadow/${this.mqttParam.clientId_basic}${type}`;
    return topic;
}
/**
 * 生成 随机字符串
 * 
 * @param {Number} len; 需要 生成的 字符串 长度； 
 */
function randomString(len) {
    len = len || 32;
    let str = "ABCDEFGHJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz012345678";
    let str_len = str.length;
    let random = "";
    for (let i = 0; i < len; i++) {
        random += str.charAt(Math.floor(Math.random() * str_len));
    }
    return random;
}
/**
 * Uint8Array 二进制数组 转字符串
 * 
 * @param {Uint8Array} fileData ; Uint8Array 二进制数组
 */
function Uint8ArrayToString(fileData) {
    var dataString = "";
    for (var i = 0; i < fileData.length; i++) {
        dataString += String.fromCharCode(fileData[i]);
    }
    return dataString
}
