<script>
    import Vue from "vue";
    import yxMqtt from "@/utils/yxMqtt";
    
    export default {
        name: 'App',
        data(){
            return {}
        },
        created () {
            // this.get_mqtt_options();
            this.mqtt_start();
            // console.log(this.$mqtt);
        },
        methods: {
            get_mqtt_options(){
                this.$http.post(`${this.$iot_api}/user_eqp`).then( (res) => {
                    // console.log(res);
                    // let mqtt_options = res.data.data;
                    // let mqtt_options = {
                    //     hostname: 'mqtt://127.0.0.1',
                    //     username: 'test',
                    //     password: '123456',
                    //     clientId: 'system',
                    // }
                    // window.eventBus.$emit('yxMqtt', mqtt_options);
                    this.mqtt_start(res)
                }).catch( (err) => {
                    console.log(err);
                })
            },
            mqtt_start(){
                let Mqtt = new yxMqtt({
                    hostname: 'wss://13xt3d9.mqtt.iot.gz.baidubce.com:443/mqtt',
                    options: {
                        username: '13xt3d9/gh_0ddfb722a565_c9edd422aafdc95f',
                        password: 'xY9Y6MQUYdCvrnJK',
                        clientId: 'gh_0ddfb722a565_c9edd422aafdc95f',
                    },
                    topic: {
                        // sub: '$baidu/iot/shadow/gh_0ddfb722a565_c9edd422aafdc95f/update/accepted',
                        sub: '$baidu/iot/shadow/gh_0ddfb722a565_c9edd422aafdc95f/update/snapshot',
                        pub: '$baidu/iot/shadow/gh_0ddfb722a565_c9edd422aafdc95f/update',
                    },
                    arrived(res) {
                        window.eventBus.$emit('baidu_shadow', res);
                    },
                    error(err) {
                        console.log(err);
                    }
                });
                Vue.prototype.$mqtt = Mqtt;
                this.$mqtt.startSub();
                this.$mqtt.Client.subscribe('$baidu/iot/shadow/gh_0ddfb722a565_c9edd422aafdc95f/get/accepted');
                this.$mqtt.Client.publish('$baidu/iot/shadow/gh_0ddfb722a565_c9edd422aafdc95f/get', JSON.stringify({ 'residired': {}, 'reported': {}}));
            }
        },
        destroyed(){
            this.$mqtt.closeSub();
        }
    }
</script>

