<template>
    <div class="page">
        <div class="page_top">
            <h4>欢迎使用！</h4>
            <span><i class="iconfont icon-dizhi"></i>{{weather.province}}&nbsp;&nbsp;&nbsp;{{weather.city}}</span>
        </div>
        <div class="weather">
            <div class="weather_temp">
                <b>{{weather.temp}}</b>
                <span>&#8451;</span>
            </div>
            <div class="weather_info">
                <p class="weather_info_main"><span id="weather_min">{{weather.weather}}</span></p>
                <p class="weather_info_list">
                    <span>温度：50 &#8451;</span>
                    <span>湿度：{{weather.humidity}} RH</span>
                    <span>风力：{{weather.winddirection}}风 {{weather.windpower}}级</span>
                </p>
            </div>
        </div>
        <div class="eqp_info">
            <div class="eqp_info_main">26 PM2.5 重度污染</div>
            <ul class="eqp_info_list">
                <li>
                    <span>设定湿度</span>
                    <b>{{eqp.humiditySet}} RH</b>
                </li>
                <li>
                    <span>当前湿度</span>
                    <b>{{eqp.humidity}} RH</b>
                </li>
                <li>
                    <span>当前温度</span>
                    <b>{{eqp.temperature1}} &#8451;</b>
                </li>
                <li>
                    <span>盘管温度</span>
                    <b>{{eqp.temperature2}} &#8451;</b>
                </li>
            </ul>
        </div>
        <div class="page_title">滤网设置</div>
        <div class="residue_date">
            <span>剩余（小时）</span>
            <b>{{eqp.airFilter}} H</b>
        </div>
        <div class="page_title">主页设置</div>
        <ul class="grid">
            <li @click="eqp_status(eqp.status)" :class="!eqp.status ? 'off' : ''">
                <i class="grid_icon iconfont icon-kaiguan"></i>
                <span class="grid_text">{{eqp.status == 1 ? '开' : '关'}}</span>
            </li>
            <li @click="eqp_fanSpeed(eqp.fanSpeed)" :class="!eqp.status ? 'forbiden' : ''">
                <template v-if="eqp.fanSpeed == 1">
                    <i class="grid_icon iconfont icon-yijifeng"></i>
                    <span class="grid_text">风速低</span>
                </template>
                <template v-else-if="eqp.fanSpeed == 2 || eqp.fanSpeed == 4">
                    <i class="grid_icon iconfont icon-erjifeng"></i>
                    <span class="grid_text">风速中</span>
                </template>
                <template v-else-if="eqp.fanSpeed == 3">
                    <i class="grid_icon iconfont icon-sanjifeng"></i>
                    <span class="grid_text">风速高</span>
                </template>
                <!-- <template v-else>
                    <i class="grid_icon iconfont icon-wufeng"></i>
                    <span class="grid_text">风速关</span>
                </template> -->
            </li>
            <li @click="eqp_fanSpeed_auto(eqp.fanSpeed_auto)" :class="!eqp.status ? 'forbiden' : ''">
                <template v-if="eqp.fanSpeed_auto">
                    <i class="grid_icon iconfont icon-A"></i>
                    <span class="grid_text">自动</span>
                </template>
                <template v-else>
                    <i class="grid_icon iconfont icon-M"></i>
                    <span class="grid_text">手动</span>
                </template>
            </li>
            <router-link tag="li" to="/setting/eqpshare" :class="!eqp.status ? 'forbiden' : ''">
                <i class="grid_icon iconfont icon-fenxiang"></i>
                <span class="grid_text">共享</span>
            </router-link>
            <li @click="show_popup"  :class="!eqp.status ? 'forbiden' : ''">
                <i class="grid_icon iconfont icon-neichushi"></i>
                <span class="grid_text">内除湿</span>
            </li>
            <router-link tag="li" to="/setting">
                <i class="grid_icon iconfont icon-shezhi"></i>
                <span class="grid_text">设置</span>
            </router-link>
        </ul>

        <!-- 设置 湿度 -->
        <mt-popup v-model="showPopup" position="bottom" :close-on-click-modal="false" class="popup_humidity not_select">
            <div class="toolbar">
                <span @click="humiditySet_cancle" class="toobar_cancle">取消</span>
                <span @click="humiditySet_confirm" class="toobar_confirm">确定</span>
            </div>
            <div class="humidity_number">设置湿度：<span>{{eqp.humiditySet_last}}</span></div>
            <mt-range v-model="eqp.humiditySet_last" :min="0" :max="100" :step="1" :bar-height="5">
                <div class="hunidity_range" slot="start">0</div>
                <div class="hunidity_range" slot="end">100</div>
            </mt-range>
        </mt-popup>
    </div>
</template>

<script>
    export default {
        name: "home",
        data() {
            return {
                home_show: 0,
                // mqtt 
                Mqtt: null,
                // 页面 功能
                eqp: {
                    status: 0,
                    fanSpeed_auto: false,
                    fanSpeed_last: 1,
                    fanSpeed: 1,
                    humiditySet_last: 0,
                    humiditySet: 80,
                    humidity: 80,
                    airFilter: 60,
                    temperature1: 18,
                    temperature2: 0,
                    errorCode: 0,
                    slaveNo: null,
                },
                showPopup: false,
                // 天气
                weather: {
                    province: '上海',
                    city: '上海市',
                    weather: '晴',
                    temp: 0,
                    humidity: 0,
                    winddirection: '',
                    windpower: 5,
                },
            }
        },
        created: function () {
            if( this.$mqtt ){
                // 发布 空 , 为了获取 物影子上次数据
                this.$mqtt.Client.publish('$baidu/iot/shadow/gh_0ddfb722a565_c9edd422aafdc95f/get', JSON.stringify({ 'residired': {}, 'reported': {}}));
            }
            let _this = this;
            window.eventBus.$on('baidu_shadow', res => {
                console.log(res);
                let reported = res.message.reported;
                _this.compute_arrrived_data(reported);
            });

            // 获取 天气
            this.get_city();
        },
        mounted () {},
        methods: {
            // 处理 接收到的 数据
            compute_arrrived_data (reported) {
                // console.log(reported);
                if (('status' in reported) && 0 <= reported.status <= 1) { // 开关机 状态
                    this.eqp.status = reported.status;
                }
                if ('fanSpeed' in reported && 1 <= reported.fanSpeed <= 4) { // 风速
                    this.eqp.fanSpeed = reported.fanSpeed;
                    this.eqp.fanSpeed_auto = reported.fanSpeed == 4 ? true : false;
                }
                if ('humiditySet' in reported && 0 <= reported.humiditySet <= 100) { // 设置 湿度
                    this.eqp.humiditySet = reported.humiditySet;
                    this.eqp.humiditySet_last = reported.humiditySet;
                }
                if ('humidity' in reported) { // 当前湿度
                    this.eqp.humidity = reported.humidity;
                }
                if ('airFilter' in reported) { // 滤网 剩余 使用时间
                    this.eqp.airFilter = reported.airFilter;
                }
                if ('temperature1' in reported) { // 当前 温度
                    this.eqp.temperature1 = reported.temperature1;
                }
                if ('temperature2' in reported) { // 盘管 温度
                    this.eqp.temperature2 = reported.temperature2;
                }
                if ('errorCode' in reported) { // 故障代码
                    // return false;
                    var errorCode = reported.errorCode
                    this.eqp.errorCode = errorCode;
                    if (errorCode == 0) {
                        return false;
                    }
                    var error_text = '';
                    switch (errorCode) {
                        case 1: error_text = '盘管温度传感器故障（E1）'; break;
                        case 2: error_text = '湿度传感器故障（E2）'; break;
                        case 3: error_text = '排水保护故障（E0）'; break;
                        case 4: error_text = '低压报警（E4）'; break;
                        case 5: error_text = '高温报警，环境温度 > 41 &#8451;（E8）'; break;
                        default: error_text = '未知故障（' + errorCode + '）'; break;
                    }
                    this.$toast({
                        message: error_text,
                        duration: 1500,
                    });
                }
                if ('slaveNo' in reported) { // 从机 地址
                    this.eqp.slaveNo = reported.slaveNo;
                }
            },
            // 发送 数据
            publish(toast_text, payload){
                if( this.eqp.status == 0 && !( 'status' in payload) ){
                    this.$toast({
                        message: '设备已关机',
                        duration: 1000,
                    });
                    return false;
                }
                this.$mqtt.publish({'desired': payload});
                // this.Mqtt.publish({'desired': payload});
                this.$toast({
                    message: toast_text,
                    duration: 1000,
                });
            },

            // 开关机
            eqp_status(val) {
                val = val == 1 ? 0 : 1;
                let toast_text = val == 1 ? '开机' : '关机';
                this.publish(toast_text, {"status": val});
            },
            // 设定 风速
            eqp_fanSpeed (val) {
                var toast_text = '自动模式无法控制风速';
                if (val != 4) {
                    val = ++val > 3 ? 1 : val;
                    toast_text = '风速 ' + val;
                    this.publish(toast_text, {"fanSpeed": val});
                }
                this.$toast({
                    message: toast_text,
                    duration: 1000,
                });
            },
            // 设定 风机 自动
            eqp_fanSpeed_auto (val) {
                // 是否 切换为 自动
                if (!val) {
                    this.eqp.fanSpeed_last = this.eqp.fanSpeed;
                    val = 4;
                } else {
                    val = this.eqp.fanSpeed_last;
                }
                let toast_text = val == 4 ? '自动' : '手动';
                this.publish(toast_text, {"fanSpeed": val});
            },
            // 新风除湿
            eqp_newWindow () {},
            // 设定 湿度
            show_popup () {
                this.eqp.humiditySet_last = this.eqp.humiditySet;
                this.showPopup = true;
            },
            humiditySet_cancle () {
                this.showPopup = false;
                this.eqp.humiditySet_last = this.eqp.humiditySet;
            },
            humiditySet_confirm () {
                // this.eqp.humiditySet = this.eqp.humiditySet_last;
                var humidity = this.eqp.humiditySet_last;
                this.showPopup = false;
                let toast_text = '设定湿度 ' + humidity + ' RH';
                this.publish(toast_text, {"humiditySet": humidity});
            },
 

            // 获取 天气信息
            get_city () {
                this.$http.get('https://restapi.amap.com/v3/ip?&key=ff4317d533ad3d2b74756c4eb7643668').then( city => {
                    // console.log(city.data);
                    this.get_weather(city.data);
                }).catch( error => {
                    console.log(error);
                });
            },
            get_weather(city) {
                // console.log(city);
                this.$http.post('https://restapi.amap.com/v3/weather/weatherInfo?&key=ff4317d533ad3d2b74756c4eb7643668', {
                    city: city.adcode,
                    extensions: 'base',
                }).then( res => {
                    // console.log(res.data);
                    var weather = res.data.lives[0];
                    this.weather.province = weather.province;
                    this.weather.city = weather.city;
                    this.weather.weather = weather.weather;
                    this.weather.temp = weather.temperature;
                    this.weather.humidity = weather.humidity;
                    this.weather.winddirection = weather.winddirection;
                    this.weather.windpower = weather.windpower;
                }).catch( err => {
                    console.log(err);
                });
            },
        },
        destroyed(){
            window.eventBus.$off('baidu_shadow');
        }
    };
</script>

